.popup__title {
  margin: 0;
  padding: 0;
  padding: 0 20px;
  font-size: 30px;
  line-height: 40px;
  text-align: center;
  margin-bottom: 25px; }

.popup__text {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 24px;
  margin-bottom: 25px;
  padding: 0 5px; }

.popup__text--review {
  padding: 0 100px;
  margin-bottom: 0; }

.popup__btn {
  display: block;
  width: 272px;
  height: 58px;
  background-color: #81B3D2;
  text-align: center;
  text-transform: uppercase;
  margin: 0 auto;
  color: #FFFFFF;
  font-size: 21px;
  line-height: 26px;
  font-family: 'PT Sans', Arial, Helvetica, sans-serif;
  font-weight: 700;
  border: 0;
  padding: 0; }

.popup__btn--review {
  width: 458px;
  margin: 0 auto; }

.h1 {
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
  margin: 0;
  margin-bottom: 26px; }
  @media (min-width: 768px) {
    .h1 {
      font-size: 30px;
      line-height: 38px; } }

.h1--form {
  margin-bottom: 28px; }

.wrapper {
  display: flex;
  flex-direction: column;
  max-width: 1200px;
  width: 100%;
  min-height: 100%;
  margin: 0 auto;
  background: #FFFFFF;
  box-shadow: 0px 5px 15px rgba(0, 1, 1, 0.2); }
